				desc4 : '◎ 现为中国工艺美术家协会会员，京西文化发展协会会员、北京青年基督教白雪美术协会会员、西藏自治区书画艺术研究院研究员。',
    			
    		},
    	],
    }
  },
  mounted () {
  	setTimeout(()=>{	
  		this.$refs.a[0].style.right="0"
  	},300)
  }
}




